.filter {
    font-family: $opsans;
    color: #000;

    background-color: #f5f5f5;
}

.filter__row {
    padding: 20px 15px;

    &:first-child {
        border-bottom: 1px solid #d6d6d6;
    }
}

.filter__categories {
    display: flex;
    flex-flow: row wrap;
    margin-bottom: -5px;
}

.filter__category {
    margin-right: 15px;
    margin-bottom: 5px;

    color: #000;
    font-size: 14px;
    font-weight: 400;
    text-decoration: none;

    transition: color 300ms ease;

    &:last-child {
        margin-right: 0;
    }

    &.active,
    &:hover,
    &:active,
    &:focus {
        color: $themecolor;
        text-decoration: none;
    }
}

.filter__sorting {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    margin-bottom: -5px;
}

.filter__sort-type {
    margin-right: 20px;
    margin-bottom: 5px;

    &:last-child {
        margin-right: 0;
        margin-left: auto;
    }
}

@media #{$tablet} {
    .filter {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .filter__row {
        padding: 25px 30px;

        &:first-child {
            border-bottom: none;
        }
    }

    .filter__category {
        margin-bottom: 0;
        margin-right: 20px;

        font-size: 16px;
    }

    .filter__sorting {
        margin-bottom: 0;
    }

    .filter__sort-type {
        margin-bottom: 0;
    }
}